"use client";

import { useSonos } from "@/components/sonos-provider";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import { X, Volume2, VolumeX, Volume1 } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { useState, useEffect } from "react";
import { cn } from "@/lib/utils";

interface VolumeControlProps {
  selectedZoneIds: string[];
  compact?: boolean;
}

export function VolumeControl({ 
  selectedZoneIds,
  compact = false 
}: VolumeControlProps) {
  const { groups, refreshState } = useSonos();
  const [isOpen, setIsOpen] = useState(false);
  const [zoneVolumes, setZoneVolumes] = useState<Record<string, number>>({});
  const [isLoading, setIsLoading] = useState(false);

  // Fetch volumes for all selected zones
  useEffect(() => {
    if (isOpen && selectedZoneIds.length > 0) {
      fetchZoneVolumes();
    }
  }, [isOpen, selectedZoneIds]);

  const fetchZoneVolumes = async () => {
    setIsLoading(true);
    try {
      const volumePromises = selectedZoneIds.map(async (zoneId) => {
        try {
          const res = await fetch(`/api/sonos/volume?groupId=${zoneId}`);
          if (res.ok) {
            const data = await res.json();
            return { zoneId, volume: data.volume || 50 };
          }
        } catch (error) {
          console.error(`Failed to fetch volume for zone ${zoneId}:`, error);
        }
        return { zoneId, volume: 50 }; // Default volume
      });

      const volumes = await Promise.all(volumePromises);
      const volumeMap: Record<string, number> = {};
      volumes.forEach(({ zoneId, volume }) => {
        volumeMap[zoneId] = volume;
      });
      setZoneVolumes(volumeMap);
    } catch (error) {
      console.error("Failed to fetch zone volumes:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleVolumeChange = async (zoneId: string, value: number[]) => {
    const newVolume = value[0];
    
    // Update local state immediately for responsive UI
    setZoneVolumes(prev => ({ ...prev, [zoneId]: newVolume }));
    
    try {
      const res = await fetch(`/api/sonos/control?groupId=${zoneId}&volume=${newVolume}`, { 
        method: "POST" 
      });
      
      if (res.status === 410) {
        // Group no longer exists, refresh state
        await refreshState();
        await fetchZoneVolumes();
      } else if (res.ok) {
        // Volume set successfully
        await refreshState();
      } else {
        // Revert on error
        await fetchZoneVolumes();
      }
    } catch (error) {
      console.error(`Failed to set volume for zone ${zoneId}:`, error);
      // Revert on error
      await fetchZoneVolumes();
    }
  };

  const getVolumeIcon = (volume: number) => {
    if (volume === 0) return VolumeX;
    if (volume < 50) return Volume1;
    return Volume2;
  };

  const getVolumeLabel = (volume: number) => {
    if (volume === 0) return "Muted";
    return `${Math.round(volume)}%`;
  };

  // Get zones that are selected
  const selectedZones = groups.filter((group: any) => 
    selectedZoneIds.includes(group.id)
  );

  // Calculate average volume for display
  const averageVolume = selectedZones.length > 0
    ? Math.round(
        selectedZones.reduce((sum: number, group: any) => {
          return sum + (zoneVolumes[group.id] || 50);
        }, 0) / selectedZones.length
      )
    : 50;

  return (
    <div className="relative">
      <Button
        variant="ghost"
        onClick={() => setIsOpen(!isOpen)}
        className={cn(
          "flex items-center gap-2 transition-all text-foreground hover:bg-foreground/10",
          compact ? "h-10 px-3 text-xs" : "h-12 px-4"
        )}
      >
        <Volume2 className="h-4 w-4 text-foreground opacity-90" />
        <span className="font-black uppercase tracking-tight text-foreground opacity-90">
          {averageVolume}%
        </span>
      </Button>

      <AnimatePresence>
        {isOpen && (
          <>
            <div 
              className="fixed inset-0 z-[250] bg-black/40 backdrop-blur-sm"
              onClick={() => setIsOpen(false)}
            />
            <motion.div
              initial={{ opacity: 0, y: compact ? 20 : -20, scale: 0.95 }}
              animate={{ opacity: 1, y: 0, scale: 1 }}
              exit={{ opacity: 0, y: compact ? 20 : -20, scale: 0.95 }}
              transition={{ duration: 0.2 }}
              className={cn(
                "fixed z-[251] bg-background/98 backdrop-blur-xl rounded-2xl border-2 border-foreground/10 shadow-2xl overflow-hidden flex flex-col",
                compact 
                  ? "bottom-[200px] right-4 sm:right-12 lg:right-20 w-[360px] max-w-[calc(100vw-2rem)] max-h-[70vh]" 
                  : "top-20 right-4 sm:right-6 md:right-8 w-[420px] max-w-[calc(100vw-2rem)] max-h-[80vh]"
              )}
            >
              {/* Header */}
              <div className="p-5 pb-4 border-b border-foreground/10 shrink-0">
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center gap-3">
                    <h3 className="text-sm font-black uppercase tracking-[0.15em] text-foreground">
                      VOLUME CONTROL
                    </h3>
                    {selectedZoneIds.length > 0 && (
                      <motion.div
                        initial={{ scale: 0 }}
                        animate={{ scale: 1 }}
                        className="h-7 w-7 rounded-full bg-primary flex items-center justify-center shadow-md"
                      >
                        <span className="text-xs font-black text-primary-foreground">
                          {selectedZoneIds.length}
                        </span>
                      </motion.div>
                    )}
                  </div>
                  <Button
                    variant="ghost"
                    size="icon"
                    className="h-10 w-10 text-foreground hover:bg-foreground/10 active:scale-95 transition-all rounded-xl touch-manipulation"
                    onClick={() => setIsOpen(false)}
                  >
                    <X className="h-5 w-5" />
                  </Button>
                </div>
              </div>

              {/* Zones Volume Controls */}
              <div className="flex-1 overflow-y-auto no-scrollbar p-5 pb-5">
                {isLoading ? (
                  <div className="flex flex-col items-center justify-center py-12 text-center">
                    <Volume2 className="h-12 w-12 text-muted-foreground opacity-20 mb-3 animate-pulse" />
                    <p className="text-sm font-medium text-muted-foreground">Loading volumes...</p>
                  </div>
                ) : selectedZones.length === 0 ? (
                  <div className="flex flex-col items-center justify-center py-12 text-center">
                    <Volume2 className="h-12 w-12 text-muted-foreground opacity-20 mb-3" />
                    <p className="text-sm font-medium text-muted-foreground">No zones selected</p>
                    <p className="text-xs text-muted-foreground opacity-70 mt-1">Select zones to control volume</p>
                  </div>
                ) : (
                  <div className="space-y-6">
                    {selectedZones.map((group: any, idx: number) => {
                      const volume = zoneVolumes[group.id] || 50;
                      const VolumeIcon = getVolumeIcon(volume);
                      const isPlaying = group.playbackState === "PLAYBACK_STATE_PLAYING";

                      return (
                        <motion.div
                          key={group.id}
                          initial={{ opacity: 0, y: 10 }}
                          animate={{ opacity: 1, y: 0 }}
                          transition={{ delay: idx * 0.05 }}
                          className="space-y-3 p-4 rounded-2xl bg-foreground/[0.03] border-2 border-foreground/10 dark:bg-foreground/[0.05]"
                        >
                          {/* Zone Header */}
                          <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                              <div className={cn(
                                "h-10 w-10 rounded-full flex items-center justify-center transition-all",
                                isPlaying 
                                  ? "bg-primary/20" 
                                  : "bg-foreground/[0.08] dark:bg-foreground/[0.12]"
                              )}>
                                <VolumeIcon className={cn(
                                  "h-5 w-5",
                                  isPlaying ? "text-primary" : "text-foreground opacity-70"
                                )} />
                              </div>
                              <div className="flex flex-col">
                                <span className="text-sm font-black uppercase tracking-tight text-foreground">
                                  {group.name}
                                </span>
                                <span className="text-xs font-medium text-muted-foreground opacity-70">
                                  {getVolumeLabel(volume)}
                                </span>
                              </div>
                            </div>
                            {isPlaying && (
                              <motion.div
                                animate={{ opacity: [0.5, 1, 0.5] }}
                                transition={{ repeat: Infinity, duration: 1.5 }}
                                className="h-2 w-2 rounded-full bg-primary"
                              />
                            )}
                          </div>

                          {/* Volume Slider */}
                          <div className="flex items-center gap-4 px-2">
                            <VolumeX 
                              className={cn(
                                "h-5 w-5 shrink-0 transition-colors",
                                volume === 0 
                                  ? "text-foreground" 
                                  : "text-muted-foreground opacity-50"
                              )}
                            />
                            <Slider 
                              value={[volume]} 
                              onValueChange={(value) => handleVolumeChange(group.id, value)}
                              max={100} 
                              step={1} 
                              className="flex-1 h-2 cursor-pointer touch-manipulation" 
                            />
                            <Volume2 
                              className={cn(
                                "h-5 w-5 shrink-0 transition-colors",
                                volume > 0 
                                  ? "text-foreground" 
                                  : "text-muted-foreground opacity-50"
                              )}
                            />
                          </div>
                        </motion.div>
                      );
                    })}
                  </div>
                )}
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
}
